function resolveComWixpressMembersCustomfieldsCustomFieldsUrl(
  opts: Omit<ResolveUrlOpts, 'domainToMappings'>
) {
  const domainToMappings = {
    'manage._base_domain_': [
      {
        srcPath: '/_api/members/v1/custom-fields',
        destPath: '/v1/custom-fields',
      },
    ],
    _: [
      {
        srcPath: '/_api/members/v1/custom-fields',
        destPath: '/v1/custom-fields',
      },
    ],
    'www._base_domain_': [
      {
        srcPath: '/_api/members/v1/custom-fields',
        destPath: '/v1/custom-fields',
      },
    ],
    'www.wixapis.com': [
      {
        srcPath: '/members/v1/custom-fields',
        destPath: '/v1/custom-fields',
      },
    ],
    'editor-flow.wixapps.net': [
      {
        srcPath: '/_api/members/v1/custom-fields',
        destPath: '/v1/custom-fields',
      },
    ],
    'api._api_base_domain_': [
      {
        srcPath: '/members/v1/custom-fields',
        destPath: '',
      },
    ],
    'editor.wixapps.net': [
      {
        srcPath: '/_api/members/v1/custom-fields',
        destPath: '/v1/custom-fields',
      },
    ],
  };

  return resolveUrl(Object.assign(opts, { domainToMappings }));
}

const PACKAGE_NAME = '@wix/auto_sdk_members_custom-fields';

/** Creates a custom field. */
export function createCustomField(payload: object): RequestOptionsFactory<any> {
  function __createCustomField({ host }: any) {
    const serializedData = transformPaths(payload, [
      {
        transformFn: transformSDKTimestampToRESTTimestamp,
        paths: [{ path: 'field.createdDate' }, { path: 'field.updatedDate' }],
      },
    ]);
    const metadata = {
      entityFqdn: 'wix.members.v1.custom_field',
      method: 'POST' as any,
      methodFqn:
        'com.wixpress.members.customfields.CustomFields.CreateCustomField',
      packageName: PACKAGE_NAME,
      url: resolveComWixpressMembersCustomfieldsCustomFieldsUrl({
        protoPath: '/v1/custom-fields',
        data: serializedData,
        host,
      }),
      data: serializedData,
      transformResponse: (payload: any) =>
        transformPaths(payload, [
          {
            transformFn: transformRESTTimestampToSDKTimestamp,
            paths: [
              { path: 'field.createdDate' },
              { path: 'field.updatedDate' },
            ],
          },
        ]),
    };

    return metadata;
  }

  return __createCustomField;
}

