/**
 * Retrieves a list of custom fields and fields provided by Members Area, given the provided paging.
 *
 * The fields are ordered by section in the following sequence:
 * - `GENERAL`
 * - `DISPLAY_INFO`
 * - `SOCIAL`
 *
 * The fields in the same section are ordered by the field creation date.
 *
 * To change the order of fields in sections, call Update Custom Fields Order.
 */
export function listCustomFields(payload: object): RequestOptionsFactory<any> {
  function __listCustomFields({ host }: any) {
    const metadata = {
      entityFqdn: 'wix.members.v1.custom_field',
      method: 'GET' as any,
      methodFqn:
        'com.wixpress.members.customfields.CustomFields.ListCustomFields',
      packageName: PACKAGE_NAME,
      url: resolveComWixpressMembersCustomfieldsCustomFieldsUrl({
        protoPath: '/v1/custom-fields',
        data: payload,
        host,
      }),
      params: toURLSearchParams(payload),
      transformResponse: (payload: any) =>
        transformPaths(payload, [
          {
            transformFn: transformRESTTimestampToSDKTimestamp,
            paths: [
              { path: 'fields.createdDate' },
              { path: 'fields.updatedDate' },
            ],
          },
        ]),
    };

    return metadata;
  }

  return __listCustomFields;
}

/**
 * Updates a custom field.
 *
 * For custom fields you can update the following fields: `name`, `defaultPrivacy`, and `socialType`.
 *
 * For system fields you can only update the `defaultPrivacy` field.
 */
export function updateCustomField(payload: object): RequestOptionsFactory<any> {
  function __updateCustomField({ host }: any) {
    const serializedData = transformPaths(payload, [
      {
        transformFn: transformSDKTimestampToRESTTimestamp,
        paths: [{ path: 'field.createdDate' }, { path: 'field.updatedDate' }],
      },
    ]);
    const metadata = {
      entityFqdn: 'wix.members.v1.custom_field',
      method: 'PATCH' as any,
      methodFqn:
        'com.wixpress.members.customfields.CustomFields.UpdateCustomField',
      packageName: PACKAGE_NAME,
      url: resolveComWixpressMembersCustomfieldsCustomFieldsUrl({
        protoPath: '/v1/custom-fields/{field.id}',
        data: serializedData,
        host,
      }),
      data: serializedData,
      transformResponse: (payload: any) =>
        transformPaths(payload, [
          {
            transformFn: transformRESTTimestampToSDKTimestamp,
            paths: [
              { path: 'field.createdDate' },
              { path: 'field.updatedDate' },
            ],
          },
        ]),
    };

    return metadata;
  }

  return __updateCustomField;
}

